import * as React from "react";
import { useUserInfo } from "shared/components/save";
import Defer from "shared/components/ui/Defer";
import Navbar from "shared/components/ui/Navbar";
import UserIcon from '@material-ui/icons/AccountCircle'
import { Breadcrumbs, List, ListItem, ListItemIcon, ListItemText, Typography } from "@material-ui/core";
import { navigate } from "gatsby";

const Page = () => {
    const data = useUserInfo();
    const logout = () => {
        localStorage.clear();
        navigate('/user/login/');
    }
    return (
        <Defer data={data}>
            {() => (
                <div>
                    <Navbar />
                    <List component="nav">
                        <ListItem>
                            <Breadcrumbs>
                                <Typography color="textPrimary">Pengaturan Akun</Typography>
                            </Breadcrumbs>
                        </ListItem>
                        <ListItem button onClick={() => alert('Belum bisa diganti')} >
                            <ListItemIcon>
                                <UserIcon />
                            </ListItemIcon>
                            <ListItemText primary="Nama" secondary={data.name} />
                        </ListItem>
                        <ListItem button onClick={() => alert('Belum bisa diganti')} >
                            <ListItemIcon>
                                <UserIcon />
                            </ListItemIcon>
                            <ListItemText primary="NIK" secondary={data.nik} />
                        </ListItem>
                        <ListItem button onClick={() => alert('Belum bisa diganti')} >
                            <ListItemIcon>
                                <UserIcon />
                            </ListItemIcon>
                            <ListItemText primary="No. HP" secondary={data.phone} />
                        </ListItem>
                        <ListItem button onClick={() => alert('Belum bisa diganti')} >
                            <ListItemIcon>
                                <UserIcon />
                            </ListItemIcon>
                            <ListItemText primary="Kata Sandi" secondary='Tekan untuk ganti' />
                        </ListItem>
                        <ListItem button onClick={() => navigate('/user/geo')}>
                            <ListItemIcon>
                                <UserIcon />
                            </ListItemIcon>
                            <ListItemText primary="Akses Desa" secondary={data.geolookup.kelurahan || '-'} />
                        </ListItem>
                        <ListItem button onClick={() => window.open('https://app.dasawarsalink.id')}>
                            <ListItemIcon>
                                <UserIcon />
                            </ListItemIcon>
                            <ListItemText primary="Ke Aplikasi Pengguna" />
                        </ListItem>
                        <ListItem button onClick={logout}>
                            <ListItemIcon>
                                <UserIcon />
                            </ListItemIcon>
                            <ListItemText onClick={logout} primary="Keluar" style={{ flexGrow: 1 }} />
                        </ListItem>
                    </List>
                </div>
            )}
        </Defer>
    );
}

export default Page;
